/**
 * 
 */

let Content = {

    //=========================
    //
    //   
    //
    createDom: function(opts) {
        var sqlRet = opts.sqlRet,
            pageIndex = opts.pageIndex;
        return function(rootEle, pageIndex) {
            sqlRet['container'] = rootEle || opts.rootEle;
            return sqlRet;
        }
    },

    /**
     * 
     * Actionwidget
     */
    bindEvent: function() {},

    /**
     * 
     *
     * 
     *   Actionwidget
     *
     * Action
     *
     * Xut.ActionMgr.getOne(key) 
     *
     */
    autoPlay: function(scopeComplete) {
        return this.autoPlay && this.autoPlay(scopeComplete);
    },


    /**
     * 
     *
     * 
     *
     * 
     *
     * ,
     *
     * @param  {[type]} this  
     *
     */
    flipOver: function() {
        return this.flipOver();
    },

    /**
     * 
     * @return {[type]} [description]
     */
    flipComplete: function() {
        return this.flipComplete();
    },


    /**
     * 
     *
     * 1 
     *   A hotspotBind 
     *   B autoPlay  Action
     *
     * 2 
     *
     * 3 
     *
     * 
     *   2,3,1
     *
     * @param  {[type]} pageIndex    []
     * @param  {[type]} rootEle      []
     * @return {[type]}              [description]
     */
    destroy: function() {
        return this.destroy();
    },


    /**
     *  
     *
     *  pad
     *
     *  
     *      1  
     *        
     *        true, ,
     *        ,
     *
     *     2 return false,
     *
     * @param  {[type]} activeObejct [description]
     * @return {[type]}              [description]
     */
    recovery: function(opts) {
        return this.recovery && this.recovery();
    }

}

export {Content}


