// 
import {observe} from '../observer/observe'

/**
 * canvas
 * canvas,pixi
 * 
 * content
 * canvas
 * cnavas category === "Sprite" cid
 */

var Factory = function() {

    /**
     * 
     * @type {Boolean}
     */
    this.enable = false

    /**
     * content
     * @type {Array}
     */
    this.failCid = []

    //contentId
    this.cid = []

    //contentMode
    //contentcanvas
    //
    //ppt=>
    this.pptId = []

    //
    this.spiritId = []

    //widgetcontent id 
    //
    this.widgetId = []

    /**
     * cid=>wid
     * pixi
     * @type {Object}
     */
    this.collections = {}

}

observe.call(Factory.prototype);

export { Factory }



