/**
 * 
 * @param  {[type]} global [description]
 * @return {[type]}        [description]
 */
import { Action } from './action'
import { Subtitle } from './subtitle'
import { hash } from '../../util/dom'

let Player = null
let noop = function() {}
let instance = hash() //
let html5Audio

/**
 * 
 * @param {[type]} options [description]
 */
var AudioFactory = Xut.CoreObject.extend({

    //
    preRelated: function(trackId, options) {
        //end 
        //
        // options.complete
        this.innerCallback = options.innerCallback;
        //
        //
        this.outerCallback = trackId == 9999 ? options.complete : null;
    },

    //
    afterRelated: function(audio, options, controlDoms) {
        //
        if (options.data && options.data.repeat) {
            this.repeat = Number(options.data.repeat); //
        }
        //
        if (options.action) {
            this.acitonObj = Action(options);
        }
        //
        if (options.subtitles && options.subtitles.length > 0) {
            //
            this.subtitleObject = new Subtitle(audio, options, controlDoms);
        }

        //
        if (this.outerCallback) {
            this.outerCallback.call(this);
        }

    },
    //
    //phoengapcallbackProcess
    //
    callbackProcess: function(sysCommand) {
        if (this.outerCallback) { //
            this.end();
        } else {
            //
            //phonegap
            if (!Xut.plat.isAndroid && this.repeat) {
                //
                this.repeatProcess();
            } else {
                //
                //audioManager
                this.innerCallback(this);
            }
        }
    },

    //
    repeatProcess: function() {
        --this.repeat;
        this.play()
    },

    //
    play: function() {
        //flash
        if (this.audio && !this.isFlash) {
            this.status = 'playing';
            this.audio.play();
        }
        this.acitonObj && this.acitonObj.play();
    },

    //
    pause: function() {
        this.status = 'paused';
        this.audio.pause();
        this.acitonObj && this.acitonObj.pause();
    },

    //
    end: function() {
        this.status = 'ended';
        this.audio.end();
        this.audio = null;
        this.acitonObj && this.acitonObj.destroy();
    },

    //
    destroyRelated: function() {
        //
        if (this.subtitleObject) {
            this.subtitleObject.destroy()
            this.subtitleObject = null;
        }
        //
        if (this.acitonObj) {
            this.acitonObj.destroy();
            this.acitonObj = null;
        }
    }
});


/**
 * PhoneGapMedia
 * @param  {string} url 
 * @return {[type]}      [description]
 */
var _Media = AudioFactory.extend({

    init: function(options, controlDoms) {

        var url = Xut.config.audioPath() + options.url,
            trackId = options.trackId,
            self = this,
            audio;

        //
        this.preRelated(trackId, options);

        //
        audio = new GLOBALCONTEXT.Media(url, function() {
            self.callbackProcess(true);
        }, function() {
            self.callbackProcess(true);
        });

        //autoplay
        this.audio = audio;
        this.trackId = trackId;
        this.options = options;

        //
        this.afterRelated(audio, options, controlDoms);
    },
    //
    end: function() {
        if (this.audio) {
            this.audio.release();
            this.audio = null;
        }
        this.status = 'ended';
        this.destroyRelated();
    }
});


/**
 * Falsh
 * @type {[type]}
 */
var _Flash = AudioFactory.extend({
    init: function(options, controlDoms) {
        var trackId = options.trackId,
            url = Xut.config.audioPath() + options.url,
            self = this,
            audio;

        //
        this.preRelated(trackId, options);

        audio = new Audio5js({
            swf_path: './lib/data/audio5js.swf',
            throw_errors: true,
            format_time: true,
            ready: function(player) {
                this.load(url);
                //
                this.play()
                self.status = "playing"
            }
        });

        this.audio = audio;
        this.trackId = trackId;
        this.status = 'playing';
        this.options = options;

        this.isFlash = true;

        //
        this.afterRelated(audio, options, controlDoms);
    },

    end: function() {
        if (this.audio) {
            this.audio.destroy();
            this.audio = null;
        }
        this.status = 'ended';
        this.destroyRelated();
    }
})


/**
 * html5audio
 * @param  {string} url    
 * @param  {object} options 
 * @return {object}         [description]
 */
var _Audio = AudioFactory.extend({
    init: function(options, controlDoms) {
        var trackId = options.trackId,
            url = Xut.config.audioPath() + options.url,
            self = this,
            audio;

        //
        this.preRelated(trackId, options);

        if (instance[trackId]) {
            audio = Xut.fix.audio ? Xut.fix.audio : instance[trackId];
            audio.src = url;
        } else {
            //create a new Audio instance
            //ios browser Xut.fix.audio src app.js
            if (Xut.fix.audio) {
                audio = Xut.fix.audio;
                audio.src = url;
            } else {
                audio = new Audio(url);
            }

            //
            //
            if (!Xut.fix.audio) {
                instance[trackId] = audio;
            }

            audio.addEventListener('ended', function() {
                self.callbackProcess()
            }, false);

            audio.addEventListener('error', function() {
                self.callbackProcess()
            }, false);
        }

        this.audio = audio;
        this.trackId = trackId;
        this.status = 'playing';
        this.options = options;

        //
        this.afterRelated(audio, options, controlDoms);
    },

    end: function() {
        if (this.audio) {
            this.audio.pause();
            this.audio.removeEventListener('ended', this.callbackProcess, false)
            this.audio.removeEventListener('error', this.callbackProcess, false)
            this.audio = null;
        }
        this.status = 'ended';
        this.destroyRelated();
    }
})



var createUUID = function() {
    return UUIDcreatePart(4) + '-' +
        UUIDcreatePart(2) + '-' +
        UUIDcreatePart(2) + '-' +
        UUIDcreatePart(2) + '-' +
        UUIDcreatePart(6);
};

function UUIDcreatePart(length) {
    var uuidpart = "";
    for (var i = 0; i < length; i++) {
        var uuidchar = parseInt((Math.random() * 256), 10).toString(16);
        if (uuidchar.length == 1) {
            uuidchar = "0" + uuidchar;
        }
        uuidpart += uuidchar;
    }
    return uuidpart;
}


/**
 * PhoneGap js cordova Media
 * @param  {string} url 
 * @return {[type]}      [description]
 */
var _cordovaMedia = AudioFactory.extend({

    init: function(options, controlDoms) {

        var url = Xut.config.audioPath() + options.url,
            trackId = options.trackId,
            self = this,
            audio;

        this.id = createUUID();

        //
        this.preRelated(trackId, options);

        var audio = {
            startPlayingAudio: function() {
                audioHandler.startPlayingAudio(self.id, url)
            },
            pausePlayingAudio: function() {
                audioHandler.pausePlayingAudio(self.id)
            },
            release: function() {
                audioHandler.release(self.id)
            },
            /**
             * 
             * @return {[type]} [description]
             */
            expansionCurrentPosition: function() {
                return getCurrentPosition(self.id)
            }
        }

        //autoplay
        this.audio = audio;
        this.trackId = trackId;
        this.options = options;

        //
        this.afterRelated(audio, options, controlDoms);
    },

    //
    play: function() {
        if (this.audio) {
            this.status = 'playing';
            this.audio.startPlayingAudio();
        }
        this.acitonObj && this.acitonObj.play();
    },

    //
    pause: function() {
        this.status = 'paused';
        this.audio && this.audio.pausePlayingAudio();
        this.acitonObj && this.acitonObj.pause();
    },


    //
    end: function() {
        if (this.audio) {
            this.audio.release();
            this.audio = null;
        }
        this.status = 'ended';
        this.destroyRelated();
    }
});



/**
 * HTML5audio
 * @param  {[type]} success [description]
 * @param  {[type]} fail    [description]
 * @return {[type]}         [description]
 */
function supportAudio(fail) {
    try {
        var audio = new Audio("lib/data/support.mp3");
        //
        audio.addEventListener('error', function(e) {
            audio = null;
            fail()
        }, false);
    } catch (er) {}
};


//apk
if (Xut.plat.isAndroid && !Xut.plat.isBrowser) {
   html5Audio = _Media;
} else {

    // 
    if (MMXCONFIG && audioHandler) {
       html5Audio = _cordovaMedia;
    } else {
        //pc
       html5Audio = _Audio;
    }

    //2015.12.23
    //audioflash
    // supportAudio(function() {
    //     Xut.Audio = _Flash;
    // });
}

Xut.Audio =html5Audio



export {
   html5Audio
}



