import { init } from './init/index'


/**
 * 
 * @return {[type]} [description]
 */
let App = function() {
    

    //
    Xut.Version = 782;

    /**
     * html
     * contentjs
     * epub
     * @type {Object}
     */
    window.HTMLCONFIG = {}

    /**
     * 
     * 2015.10.19
     * ibooks
     * epub ibooks
     * index.html
     * 
     *   window.IBOOKSCONFIG = {
     *       compiled: true,
     *       pageIndex: 1,
     *       existSvg: true,
     *       screenSize: {
     *           "width": 718,
     *           "height": 958
     *       }
     *   }
     */
    var IBOOKSCONFIG = window.IBOOKSCONFIG;
   
    //IBOOS
    //H5
    if (IBOOKSCONFIG && IBOOKSCONFIG.data) {
        _.each(IBOOKSCONFIG.data, function(data, tabName) {
                data.item = function(index) {
                    return this[index];
                }
            })
            //iosibooks
            //isBrowser
        Xut.plat.isBrowser = true;
        Xut.plat.isIOS = false;
    }

    //ibooks
    Xut.IBooks = {

        /**
         * 
         * @return {[type]} [description]
         */
        pageIndex: function() {
            if (IBOOKSCONFIG) {
                //1
                return IBOOKSCONFIG.pageIndex + 1;
            }
        }(),

        /**
         * svg
         * @type {[type]}
         */
        existSvg: IBOOKSCONFIG ? IBOOKSCONFIG.existSvg : false,

        /**
         * ibooks
         * @return {[type]} [description]
         */
        Enabled: function() {
            return IBOOKSCONFIG ? true : false;
        }(),

        /**
         * 
         * @type {[type]}
         */
        CONFIG: IBOOKSCONFIG,

        /**
         * 
         * @return {[type]} [description]
         */
        runMode: function() {
            //ibooks
            //
            if (IBOOKSCONFIG && !IBOOKSCONFIG.compiled) {
                return true;
            }
            return false;
        },
        /**
         * 
         * @return {[type]} [description]
         */
        compileMode: function() {
            //ibooks
            //
            if (IBOOKSCONFIG && IBOOKSCONFIG.compiled) {
                return true;
            }
            return false;
        }
    }


    //ios  
    //audio.video 
    Xut.fix = Xut.fix || {};

    //
    if (Xut.plat.isBrowser && (Xut.plat.isIOS || Xut.plat.isAndroid)) {
        var fixaudio = function() {
            if (!Xut.fix.audio) {
                Xut.fix.audio = new Audio();
                Xut.fix.video = document.createElement("Video");
                document.removeEventListener('touchstart', fixaudio, false);
            }
        };
        document.addEventListener('touchstart', fixaudio, false);
    }

    init();
}


$(function() {
    App()
})



