/**
 * 
 */
if (plat.isBrowser) {
    $(document).keyup(function(event) {
        switch (event.keyCode) {
            case 37:
                Xut.View.GotoPrevSlide()
                break;
            case 39:
                Xut.View.GotoNextSlide()
                break;
        }
    })
}


