/**
 * iframe
 */

function iframeWidget(data) {

    var self = this;

    //
    _.extend(this, data)

    //
    this.$wapper = this.createWapper();

    Xut.nextTick({
        'container': self.rootNode,
        'content': this.$wapper
    }, function() {
        self.rootNode = null;
        self.bindPMS();
    });

    return this;
}

iframeWidget.prototype = {

    /**
     * 
     * @return {[type]} [description]
     */
    createWapper: function() {
        var zIndex, str, dom, ifr;

        //
        if (this.zIndex === 0) {
            zIndex = this.zIndex;
        } else {
            zIndex = this.zIndex || Xut.zIndexlevel();
        }

        this.zIndex = zIndex;

        str = '<div id="iframeWidget_{0}" style="z-index:{1};width:{2}px;height:{3}px;top:{4}px;left:{5}px;position:absolute;" ></div>';

        dom = String.format(str,
            this.id, zIndex, this.width, this.height, this.top, this.left
        );

        ifr = this.createIframe();

        this._iframe = ifr;

        return $(dom).append(ifr)
    },


    /**
     * iframe
     * @return {[type]} [description]
     */
    createIframe: function() {
        var me = this,
            path = 'content/widget/' + this.widgetId + '/index.html?xxtParaIn=' + this.key,
            ifr = document.createElement('iframe');

        ifr.id = 'iframe_' + this.id;
        ifr.src = path;
        ifr.style.width = '100%';
        ifr.style.height = '100%';
        ifr.sandbox = "allow-scripts allow-same-origin";
        ifr.frameborder = 0;
        if (ifr.attachEvent) {
            ifr.attachEvent('onload', function() {
                me.iframeComplete();
            });
        } else {
            ifr.onload = function() {
                me.iframeComplete();
            };
        }
        return ifr;
    },


    /**
     * iframe
     * @return {[type]} [description]
     */
    iframeComplete: function() {
        var me = this;
        var dataSource = this.loadData();
        var width = me._iframe.offsetWidth;
        var height = me._iframe.offsetHeight;

        if (dataSource.screenSize.width * 0.98 <= width && dataSource.screenSize.height * 0.98 <= height) {
            Xut.View.Toolbar({
                show: 'button',
                hide: 'controlBar'
            });
        } else if (dataSource.screenSize.width * 0.7 <= width && dataSource.screenSize.height * 0.7 <= height) {
            Xut.View.Toolbar({
                show: 'button'
            });
        }

        this.PMS.send({
            target: me._iframe.contentWindow,
            origin: '*',
            type: 'loadData',
            data: dataSource,
            //
            success: success,
            error: function() {}
        });

        function success() {
            // console.log('')
        }

        //iframe
        me.state = true;
    },

    /**
     * ifarme
     * @return {[type]} [description]
     */
    loadData: function() {
        var item,
            field,
            source_export = [],
            images = Xut.data['Image'],
            token = null,
            outputPara = this.inputPara,
            items = outputPara.source;

        for (item in items) {
            if (items.hasOwnProperty(item)) {
                field = {};
                token = images.item((parseInt(items[item]) || 1) - 1);
                field['img'] = '../gallery/' + token.md5;
                field['thumb'] = '';
                field['title'] = token.imageTitle;
                source_export.push(field);
            }
        }

        outputPara.source = source_export;

        return outputPara;
    },


    /********************************************************************
     *
     *                   iframe
     *
     * ********************************************************************/

    /**
     * iframe
     * @return {[type]} [description]
     */
    bindPMS: function() {
        var me = this,
            markId = this.id;

        this.PMS = PMS;
        //widget
        PMS.bind("onHideWapper" + markId, function(e) {
            var $wapper = me.$wapper;
            $wapper.hide();
            me.state = false;

        }, '*')

        //
        PMS.bind("onFullscreen" + markId, function(e) {
            var $wapper = me.$wapper,
                $iframe = $(me._iframe);

            if (!$iframe.length) return;
            //
            Xut.VideoManager.clearVideo();

            $wapper.css({
                width: '100%',
                height: '100%',
                zIndex: Xut.zIndexlevel(),
                top: 0,
                left: 0
            })

            //Widget
            if (e.full == false) {
                var body = document.body,
                    width = parseInt(body.clientWidth),
                    height = parseInt(body.clientHeight),
                    rote = me.width / me.height,
                    getRote = function(width, height, rote) {
                        var w = width,
                            h = width / rote;
                        if (h > height) {
                            h = height;
                            w = h * rote;
                        }
                        return {
                            w: parseInt(w),
                            h: parseInt(h)
                        };
                    },
                    size = getRote(width, height, rote),
                    left = (width - size.w) / 2,
                    top = (height - size.h) / 2;

                $iframe.css({
                    width: size.w,
                    height: size.h,
                    position: 'absolute',
                    top: top,
                    left: left
                });
            }
            //
            Xut.View.Toolbar("hide")

        }, '*');

        //
        PMS.bind("onReset" + markId, function(e) {
            var $wapper = me.$wapper,
                $iframe = $(me._iframe);

            if (!$iframe.length) return;

            $wapper.css({
                zIndex: me.zIndex,
                width: me.width + 'px',
                height: me.height + 'px',
                top: me.top + 'px',
                left: me.left + 'px'
            });

            //iframe
            $iframe.css({
                width: '100%',
                height: '100%',
                position: '',
                top: '0',
                left: '0'
            })

            Xut.View.Toolbar("show");

        }, '*');

        //
        PMS.bind("onShowToolbar" + markId, function(e) {
            // Xut.View.ShowToolbar();
        }, '*');

        //
        PMS.bind("onHideToolbar" + markId, function(e) {
            Xut.View.HideToolbar();
        }, '*');

        //
        PMS.bind('scrollToPage' + markId, function(data) {
            Xut.View.GotoSlide(data['ppts'], data['pageIndex'])
        }, '*');
    },


    //================================

    /**
     * 
     * 
     * @return {[type]} [description]
     */
    dispatchProcess: function() {
        if (this.state) {
            this.stop();
        } else {
            this.start();
        }
    },

    /**
     * 
     * @return {[type]} [description]
     */
    start: function() {
        var me = this;
        me.domWapper();
        this.PMS.send({
            target: me._iframe.contentWindow,
            url: me._iframe.src,
            origin: '*',
            type: 'onShow',
            success: function() {
                // alert(123)
            }
        });
        setTimeout(function() {
            me.state = true;
        }, 0)
    },

    /**
     * 
     * @return {[type]} [description]
     */
    stop: function() {
        var me = this;
        me.domWapper();
        this.PMS.send({
            target: me._iframe.contentWindow,
            url: me._iframe.src,
            origin: '*',
            type: 'onHide',
            success: function() {}
        });
        setTimeout(function() {
            me.state = false;
        }, 0)
    },


    /**
     * 
     * @return {[type]} [description]
     */
    domWapper: function() {
        if (this.state) {
            this.$wapper.hide();
        } else {
            this.$wapper.show();
        }
    },


    //
    recovery: function() {
        var me = this;
        if (me.state) {
            me.PMS.send({
                target: me._iframe.contentWindow,
                url: me._iframe.src,
                origin: '*',
                type: 'onHide',
                success: function() {}
            });
            me.domWapper();
            me.state = false;
            return true;
        }
        return false;
    },

    //
    destroy: function() {

        var me = this,
            iframe = this._iframe,
            PMS = this.PMS;

        //
        PMS.send({
            target: iframe.contentWindow,
            url: iframe.src,
            origin: '*',
            type: 'onDestory',
            success: function() {}
        });

        //
        PMS.unbind();

        //
        setTimeout(function() {
            me._iframe = null;
            me.$wapper.remove();
            me.$wapper = null;
        }, 0)
    }

}


export { iframeWidget }



