/**
 * 
 * @return {[type]} [description]
 */
var loader = function() {
    return {
        /**,
         * @param fileList:           
         * @param callback:           ,onload
         * @param scope:              
         * @param preserveOrder:      
         */
        load: function(fileList, callback, scope, preserveOrder) {
            //
            if (fileList.length && preserveOrder) {
                var temp = [];
                fileList.forEach(function(val, index) {
                    if (val) {
                        temp.push(val);
                    }
                })
                fileList = temp.reverse();
                temp = null;
            }

            var scope = scope || this,
                //var scope =this,//
                head = document.getElementsByTagName("head")[0],
                fragment = document.createDocumentFragment(),
                numFiles = fileList.length,
                loadedFiles = 0;

            //fileList
            var loadFileIndex = function(index) {
                head.appendChild(scope.buildScriptTag(fileList[index], onFileLoaded));
            };

            /**
             * ,
             */
            var onFileLoaded = function() {
                loadedFiles++;
                //
                if (numFiles == loadedFiles && typeof callback == 'function') {
                    callback.call(scope);
                } else {
                    if (preserveOrder === true) {
                        loadFileIndex(loadedFiles);
                    }
                }
            };

            if (preserveOrder === true) {
                loadFileIndex.call(this, 0);
            } else {
                for (var i = 0, len = fileList.length; i < len; i++) {
                    fragment.appendChild(this.buildScriptTag(fileList[i], onFileLoaded));
                }
                head.appendChild(fragment);
            }
        },

        //javascriptlink 
        buildScriptTag: function(filename, callback) {
            var exten = filename.substr(filename.lastIndexOf('.') + 1);
            if (exten == 'js') {
                var script = document.createElement('script');
                script.type = "text/javascript";
                script.src = filename;
                script.onload = callback;
                return script;
            }
            if (exten == 'css') {
                var style = document.createElement('link');
                style.rel = 'stylesheet';
                style.type = 'text/css';
                style.href = filename;
                callback();
                return style;
            }
        }
    };
}();



function pollCss(node, callback) {
    var sheet = node.sheet,
        isLoaded;
    var isOldWebKit = +navigator.userAgent
        .replace(/.*AppleWebKit\/(\d+)\..*/, "$1") < 536
        // for WebKit < 536
    if (isOldWebKit) {
        if (sheet) {
            isLoaded = true
        }
    }
    // for Firefox < 9.0
    else if (sheet) {
        try {
            if (sheet.cssRules) {
                isLoaded = true
            }
        } catch (ex) {
            // The value of `ex.name` is changed from "NS_ERROR_DOM_SECURITY_ERR"
            // to "SecurityError" since Firefox 13.0. But Firefox is less than 9.0
            // in here, So it is ok to just rely on "NS_ERROR_DOM_SECURITY_ERR"
            if (ex.name === "NS_ERROR_DOM_SECURITY_ERR") {
                isLoaded = true
            }
        }
    }

    setTimeout(function() {
        if (isLoaded) {
            // Place callback here to give time for style rendering
            callback()
        } else {
            pollCss(node, callback)
        }
    }, 20)
}


function addOnload(node, callback, isCSS, url) {
    var supportOnload = "onload" in node;
    var isOldWebKit = +navigator.userAgent
        .replace(/.*AppleWebKit\/(\d+)\..*/, "$1") < 536
        // for Old WebKit and Old Firefox
    if (isCSS) {
        setTimeout(function() {
                pollCss(node, callback)
            }, 1) // Begin after node insertion
        return
    }

    if (supportOnload) {
        node.onload = onload
        node.onerror = function() {
            onload()
        }
    } else {
        node.onreadystatechange = function() {
            if (/loaded|complete/.test(node.readyState)) {
                onload()
            }
        }
    }

    function onload() {
        // Ensure only run once and handle memory leak in IE
        node.onload = node.onerror = node.onreadystatechange = null
            // Remove the script to reduce memory leak
        if (!isCSS) {
            var head = document.getElementsByTagName("head")[0] || document.documentElement;
            head.removeChild(node)
        }
        // Dereference the node
        node = null
        callback()
    }
}

function request(url, callback, charset) {
    var IS_CSS_RE = /\.css(?:\?|$)/i,
        isCSS = IS_CSS_RE.test(url),
        node = document.createElement(isCSS ? "link" : "script");

    if (charset) {
        var cs = isFunction(charset) ? charset(url) : charset
        if (cs) {
            node.charset = cs
        }
    }
    addOnload(node, callback, isCSS, url)
    if (isCSS) {
        node.rel = "stylesheet"
        node.href = url
    } else {
        node.async = true
        node.src = url
    }
    // For some cache cases in IE 6-8, the script executes IMMEDIATELY after
    // the end of the insert execution, so use `currentlyAddingScript` to
    // hold current node, for deriving url in `define` call
    //currentlyAddingScript = node
    var head = document.getElementsByTagName("head")[0] || document.documentElement;
    var baseElement = head.getElementsByTagName("base")[0];
    // ref: #185 & http://dev.jquery.com/ticket/2709
    baseElement ?
        head.insertBefore(node, baseElement) :
        head.appendChild(node)
        //currentlyAddingScript = null
}

let loadfile = request

export {
    loader,
    request,
    loadfile
}



