/**
 * 2016.4.11
 * canvas
 * context
 * 
 * 1 
 * 2 
 * @type {Array}
 */
export function createNextTask(callback) {
    return {
        //
        context: {
            wait: false, //
            statas: false, //
            _ids: [],
            /**
             * 
             * @return {[type]} [description]
             */
            check: function() {
                var total = this.length();
                if (!total.length) {
                    //
                    this.statas = true;
                }
                //
                if (this.wait) {
                    callback && callback()
                    return
                }
                //
                //
                //
                if (!this.wait && this.statas) {
                    this.wait = true;
                    return;
                }
            },
            add: function(id) {
                if (-1 === this._ids.indexOf(id)) {
                    this._ids.push(id)
                }
            },
            remove: function(id) {
                if (!id) return;
                var index = this._ids.indexOf(id)
                var val = this._ids.splice(index, 1)
                this.check(val);
                return val
            },
            length: function() {
                return this._ids.length;
            }
        },
        event: [], //
        pre: {} //
    };
}



